.signup-foreground {
    display: flex;
    flex-direction: column;
    align-items: center;
    outline: 2px solid;
}

.signup-footer {
    margin-top: 20px;
    text-align: center;
}

.signup-footer > :last-child {
    margin: 10px 0 30px 0;
}

.signup-footer > * {
    font-weight: 500;
}